.filter-checkbox-input {
  position: absolute;
  z-index: -1;
  opacity: 0; }

/* для элемента label, связанного с .filter-checkbox-input */
.filter-checkbox-input + label {
  display: inline-flex;
  align-items: center;
  user-select: none; }

/* создание в label псевдоэлемента before со следующими стилями */
.filter-checkbox-input + label::before {
  content: "";
  display: inline-block;
  width: 15px;
  height: 15px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #a87b62;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%; }
  @media (max-width: 767px) {
    .filter-checkbox-input + label::before {
      width: 18px;
      height: 18px; } }

/* стили при наведении курсора на checkbox */
.filter-checkbox-input:not(:disabled):not(:checked) + label:hover::before {
  border-color: #a87b62; }

/* стили для активного чекбокса (при нажатии на него) */
.filter-checkbox-input:not(:disabled):active + label::before {
  background-color: #a87b62; }

/* стили для чекбокса, находящегося в фокусе и не находящегося в состоянии checked */
.filter-checkbox-input:focus:not(:checked) + label::before {
  border-color: #a87b62; }

/* стили для чекбокса, находящегося в состоянии checked */
.filter-checkbox-input:checked + label::before {
  border-color: #a87b62;
  background-color: #a87b62; }

/* стили для чекбокса, находящегося в состоянии disabled */
.filter-checkbox-input:disabled + label::before {
  background-color: #a87b62; }

